const regex_bold = new RegExp(/\*(\**[^*\n]+\**)\*/, 'g');
const regex_italic = new RegExp(/_(_*[^_\n]+_*)_/, 'g');
const regex_strikethrough = new RegExp(/~(~*[^~\n]+~*)~/, 'g');
const regex_linebreak = new RegExp(/\\n|\n|\r/, 'g');

const markdownToHtml = (str) => {
    if (!str) {
        return '';
    }

    return str
        .replace(regex_strikethrough, '<del>$1</del>')
        .replace(regex_italic, '<em>$1</em>')
        .replace(regex_bold, '<strong>$1</strong>')
        .replace(regex_linebreak, '<br>');
};

const textReplace = (str, markdown, value) => {
    if (!str || !markdown || !value) {
        return str;
    }

    return str.replace(markdown, value);
};

const  validarStringComSeparadores = (string) => {

    if (string.trim() === '') {
      return false;
    }
  
    const padrao = /^[\d.,;]+$/;
    if (!padrao.test(string)) {
      return false;
    }
  
  
    const arrayIds = string.split(/[.,;]/);
  
    return arrayIds;
    }
    
    const  validatePhoneNumbers = (phoneNumbers) => {
  
      const duplicates = phoneNumbers.filter(
        (phoneNumber, index) => phoneNumbers.indexOf(phoneNumber) !== index
      );
    
      if (duplicates.length > 0) {
        console.log('Números de telefone duplicados encontrados:', duplicates);
        return {
          validNumbers: [],
          invalidNumbers: phoneNumbers,
        };
      }
    
  
      const validNumbers = [];
      const invalidNumbers = [];
    
      for (const phoneNumber of phoneNumbers) {
        if (phoneNumber.startsWith("55")) {
          validNumbers.push(phoneNumber);
        } else {
          invalidNumbers.push(phoneNumber);
        }
      }
    
      return {
        validNumbers,
        invalidNumbers,
      };
    }
    

export { markdownToHtml, textReplace, validatePhoneNumbers, validarStringComSeparadores };
