import React, { useState, useEffect } from 'react';
import { BdsInput } from 'blip-ds/dist/blip-ds-react';
import guid from '../../utils/guid';
import { validarStringComSeparadores, validatePhoneNumbers } from '../../utils/string';
import { showToast } from "../../api/commomService";
import getNameSpaceWhatsApp from '../../services/getNameSpaceWhatsApp';
import setServiceTake from '../../services/setService'
import getPhonesServices from '../../services/getPhones'
import deletePhonesServices from '../../services/deletePhone'



const SendNotification = ({ shortName, accessKey1, email }) => {

  const [message, setMessage] = useState([])

  const [loading, setLoading] = useState(false);

  const [phones, setPhones] = useState([]);


  const handleGetIds = (events) => {

    setMessage(events.detail.value);
  }


  const fxGetPhones = async () => {

    const resultPhones = await getPhonesServices('pontoparse');

    if (resultPhones.data.data.telegramIds.length === 0) {
      setPhones([]);
      return
    }
    setPhones(resultPhones.data.data.telegramIds)
  }

  useEffect(() => {

    fxGetPhones();

  }, [])


  const handleSendMessage = async () => {
    setLoading(true);

    const arrayIds = message;

    const resultadoValidacao = validarStringComSeparadores(arrayIds);

    console.log(resultadoValidacao)

    const validationResults = validatePhoneNumbers(resultadoValidacao);

    const resultNameSpace = await getNameSpaceWhatsApp(guid(), localStorage.getItem('info'));

    let app = resultNameSpace?.data?.resource?.name;

    const payload = {
      telegramIds: validationResults.validNumbers,
      contract: app
    }


   if(validationResults.invalidNumbers.length > 0 ) {
    showMessage('Telefone inválido ou sem o 55', 'warning');
    return false;
   }

    const { status } = await setServiceTake(payload);

    if (status === 200) {
      fxGetPhones();
      setMessage("");
      showMessage('Configuração salva com sucesso', 'success');

      return
    }


    showMessage('Erro ao gravar as informações', 'danger');

  }


  const showMessage = (msg, type) => {

    setTimeout(() => {

      showToast({
        type: type,
        message: msg
      })
      setLoading(false);

    }, 900);

  }

  const handleDelete = async (item) => {

    const result = await deletePhonesServices('pontoparse', item);
    if (result.status === 202) {

      fxGetPhones();
      showMessage('Telefone deletado com sucesso', 'success');
      return
    }

    showMessage('Erro ao deletar o telefone', 'danger');
  }


  return (

    <>
   {phones.length > 0  &&  <> <div style={{ display: 'flex', justifyContent: 'center' }}>
        <div >
          <label style={{ fontSize: '18px' }}>Lista de telefones cadastrados</label>
        </div>

      </div>
      <div style={{ width: '100%' }}>
        <bds-paper>
          <div class="rounded-lg bg-white pt-6 pb-2">
            <div class="max-h-80 px-4 scrollbar-thin scrollbar-track-transparent scrollbar-thumb-zinc-300">
              <table class="w-full table-auto "><thead>
                <tr class="sticky top-0 z-10 border-b border-neutral-medium-silver bg-white text-base text-black">
                  <th class="py-2 font-semibold">Telefone</th>
                  <th class="py-2 font-semibold">Excluir</th>

                </tr></thead>
                <tbody >

                  {phones.map((x, index) => {
                    return (
                      <tr key={index}>
                        <td class="py-2" >{x}</td>
                        <td class="py-2" > <bds-button-icon variant="secondary" size="small" icon="trash" onClick={() => handleDelete(x)}></bds-button-icon></td>
                      </tr>
                    )
                  })}


                </tbody>
              </table>
            </div>
          </div>

        </bds-paper>

      </div><hr></hr></>}

      <br></br>

      <div style={{ display: 'flex', justifyContent: 'center' }}>
        <div >
          <label style={{ fontSize: '18px' }}>Cadastro de telefones para notificação de inatividade!</label>
        </div>

      </div>
      <div className="container w-100">
        <div className="row">

          <div className="col-12">
            <div className="col-12">
              <label>Exemplo: 5511970950054,5511970950044,5511970950012</label>
            </div>
            <BdsInput
              label="Insira os telefones que você quer notificar!"
              value={message}
              onBdsChange={(event) => handleGetIds(event)}
              isTextarea="true"
              rows={8}

            />

          </div>

          <hr />



        </div>
        <div style={{ display: 'flex', justifyContent: 'end' }}>

          <bds-button icon="save-disk" size="short" onClick={handleSendMessage}>Salvar</bds-button>

        </div>
      <br />
        <span><strong>OBS:</strong> Após seguir os passos acima, clique no link abaixo para instalar o plugin na loja do Google Chrome</span><br /><br />
        <span ng-if="tab.tabHref">
          <a href="https://chrome.google.com/webstore/detail/plugin-desk/bmjjlaomahkobdcmgnbihomlipaoolnh?hl=pt-BR" class="tab-href" target="_blank" style={{ color: 'black' }}>Clique aqui! <i class="icon-external-link icon icon-xss mb2"></i></a>
        </span>
      </div>

    </>

  )

};

export default SendNotification;